:root {
  --default-shadow: rgba(0,0,0,0.06) 0px 1px 5px 3px;
}

#app-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

#main-header {
  display: flex;
  justify-content: center;
  margin: 12px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 5px;
}

@media only screen and (max-width: 290px) {
  #main-header {
    font-size: 22px;
  }
}

.start-game-button {
  border: none;
  outline: none;
  background-color: rgb(30, 173, 85);
  color: white;
  font-family: inherit;
  font-size: 16px;
  border-radius: 6px;
  padding: 8px 14px;
  box-shadow: var(--default-shadow);
}

.start-game-button:hover {
  background-color: rgb(14, 143, 63);
}