@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: "Work Sans", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

svg {
  display: inline-block;
  vertical-align: middle;
}

@keyframes fade-in {
  from { opacity: 0 } to { opacity: 1 }
}
:root {
  --default-shadow: rgba(0,0,0,0.06) 0px 1px 5px 3px;
}

#app-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

#main-header {
  display: flex;
  justify-content: center;
  margin: 12px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 5px;
}

@media only screen and (max-width: 290px) {
  #main-header {
    font-size: 22px;
  }
}

.start-game-button {
  border: none;
  outline: none;
  background-color: rgb(30, 173, 85);
  color: white;
  font-family: inherit;
  font-size: 16px;
  border-radius: 6px;
  padding: 8px 14px;
  box-shadow: rgba(0,0,0,0.06) 0px 1px 5px 3px;
  box-shadow: var(--default-shadow);
}

.start-game-button:hover {
  background-color: rgb(14, 143, 63);
}
#end-screen {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  animation: fade-in forwards 1s linear;
}

#end-score {
  margin-top: 12px;
  border: solid 1px gray;
  border-radius: 10px;
  padding: 8px 15px;
  box-shadow: var(--default-shadow);
  font-weight: 600;
}
#user-ranking {
  font-size: 30px;
  font-weight: 600;
}

#leaderboard-container {
  opacity: 0;
  animation: fade-in forwards 0.8s linear;
}

#leaderboard {
  border: solid 1px gray;
  border-radius: 10px;
  box-shadow: var(--default-shadow), white 0 0 0 10px;
  margin-bottom: 30px;
}

#leaderboard-header {
  font-size: 20px;
  font-weight: 600;
  margin: 20px 0 5px 0;
}

.leaderboard-listing {
  display: grid;
  align-items: center;
  grid-template-columns: 2.5fr 10fr 4fr;
  border-bottom: solid 1px gray;
}

.leaderboard-listing:last-child {
  border-bottom: none;
}

.leaderboard-number {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: rgb(70, 70, 70);
  background-color: rgb(228, 228, 228);
  box-shadow: rgb(228, 228, 228) 0 1px 0 2px, gray 2px 0 0 1px;
  z-index: -1;
}

.leaderboard-listing:first-child .leaderboard-number {
  background-color: rgb(75, 155, 95);
  color: white;
  box-shadow: rgb(75, 155, 95) 0 1px 0 2px, gray 2px 0 0 1px;
}

.leaderboard-listing:nth-child(2) .leaderboard-number {
  background-color: rgb(151, 206, 164);
  color: rgb(38, 49, 41);
  box-shadow: rgb(151, 206, 164) 0 1px 0 2px, gray 2px 0 0 1px;
}

.leaderboard-listing:nth-child(3) .leaderboard-number {
  background-color: rgb(189, 218, 195);
  color: rgb(50, 54, 51);
  box-shadow: rgb(187, 211, 192) 0 1px 0 2px, gray 2px 0 0 1px;
}

.leaderboard-name {
  padding: 5px 10px;
  width: 150px; 
  overflow: hidden;
}

.leaderboard-score {
  text-align: right;
  padding-right: 10px;
  font-weight: 600;
  color: rgb(97, 97, 97);
}

.user-leaderboard-listing .leaderboard-name {
  font-weight: bold;
  font-style: italic;
}

#new-best-text {
  margin-top: -15px;
  margin-bottom: 20px;
  font-weight: 600;
  font-style: italic;
  font-size: 16px;
  color: rgb(27, 99, 17);
  text-align: center;
  width: 200px;
  padding-bottom: 20px;
  border-bottom: solid 1px rgb(190, 190, 190);
}
#map-container {
  --country-active-color: rgb(255, 224, 166);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#main-map {
  width: 100vh;
  max-width: 95%;
  border: solid 1px rgb(160, 160, 160);
  border-radius: 10px;
  box-shadow: var(--default-shadow);
  cursor: move;
}

.geography {  
  stroke: rgb(145, 145, 145);
  stroke-width: 0.5px;
  outline: none;
  cursor: default;
  transition: fill 0.2s;
}

.country-answer-option:hover {
  fill: var(--country-active-color);
  cursor: pointer;
}

.country-answered:hover {
  fill: rgb(160, 217, 240);
  cursor: pointer;
}

.country-completed {
  fill: rgb(190, 222, 190);
}

.country-completed:hover {
  fill: rgb(122, 196, 122);
}

.country-active {
  fill: var(--country-active-color);
}
#answer-selector {
  --border-color: rgb(173, 173, 173);
  visibility: hidden;
  position: fixed;
  background-color: transparent;
  border: none;
  padding: 25px;
  margin: -25px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

#answer-selector-prompt-text {
  background-color: rgba(0, 0, 0, 0.7);
  font-size: 13px;
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
}

.answer-button {
  display: block;
  width: 100%;
  padding: 3px 10px;
  margin: 5px 0;
  font-family: inherit;
  font-size: 15px;
  border-radius: 20px;
  border: none;
  border-bottom: solid 1px var(--border-color);
  box-shadow: var(--default-shadow);
  background-color: white;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.answer-button:hover {
  background-color: rgb(176, 223, 241);
}

.answer-button:focus {
  outline: none;
}
#guess-submit {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#answer-options-list {
  min-height: 50px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

#find-text {
  position: relative;
  right: 25px;
  width: 0;
  color: rgb(65, 65, 65);
  font-weight: bold;
  margin-right: 0;
}

@media only screen and (max-width: 600px) {
  #answer-options-list {
    flex-direction: column;
  }

  #find-text {
    right: 18px;
  }
}

#answer-options-list > * {
  font-size: 15px;
  box-sizing: border-box;
  padding: 4px 10px;
  margin: 10px 6px;
  border-radius: 20px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

#submit-guess-button {
  padding: 5px 10px;
  margin: 5px;
  font-size: 16px;
  font-family: inherit;
  background-color: rgb(195, 240, 208);
  border: solid 1px rgb(107, 151, 120);
  color: rgb(51, 70, 57);
  border-radius: 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  box-shadow: var(--default-shadow);
}
#submit-guess-button:enabled:hover {
  background-color: rgb(152, 221, 172);
}
#submit-guess-button:disabled {
  color: inherit;
  opacity: 0.45;
}
#submit-guess-button:focus {
  outline: none;
}

.answer-option-unselected {
  background-color: rgb(255, 240, 211);
  color: rgb(155, 135, 101);
}

.answer-option-selected {
  background-color: rgb(41, 151, 194);
  color: white;
}

.life {
  margin: 0 8px;
  margin-right: -1px;
  font-size: 18px;
}

.life-active {
  color: rgb(235, 131, 166);
}

.life-lost {
  animation: lose-life forwards 0.9s cubic-bezier(.16,1.13,.73,.89);
}

@keyframes lose-life {
  0% {
    color: rgb(255, 93, 93);
    transform: scale(1);
  }

  50% {
    color: rgb(230, 21, 21);
    transform: scale(1.2);
  }

  100% {
    color: rgb(200, 200, 200);
    transform: scale(1);
  }
}
#answer-result-popup-container {
  position: fixed;
  top: 140px;
  width: 100%;
  display: flex;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
}

#answer-result-popup-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.85);
  border: solid 1px lightgray;
  box-shadow: var(--default-shadow);
  border-radius: 5px;
  padding: 10px;
}

#answer-result-icon {
  font-size: 40px;
}

#answer-result-main-text {
  font-weight: 600;
  font-size: 20px;
}

#answer-result-sub-text {
  font-size: 16px;
  color: rgb(99, 99, 99);
}

.answer-result-displayed {
  visibility: visible;
  animation: display-result 1.8s forwards ease-in-out;
}

@keyframes display-result {
  0% {
    opacity: 0;
    transform: translateX(-40px);
  }

  25%, 75% {
    opacity: 1;
    transform: none;
  }

  100% {
    opacity: 0;
    transform: translateX(40px);
  }
}
#game-screen-container {
  transition: opacity 3s cubic-bezier(.8,.1,.96,.54);
}

.game-hud-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 2px;
}

.game-hud-content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  padding: 0 12px;
  border: solid 1px rgb(160, 160, 160);
  border-bottom: none;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.game-hud-label {
  margin-right: 6px;
  color: rgb(117, 117, 117);
}
.footer {
  margin-top: auto;
  margin-bottom: 5px;
  min-height: 20px;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer > * {
  margin: 0px 5px;
}
.title-a {
  color: rgb(48, 187, 241);
}

.title-b {
  color: rgb(36, 137, 177);
}

.title-c {
  color: rgb(21, 89, 116);
}

.title-d {
  color: rgb(11, 44, 58);
}
#sign-in-section {
  position: relative;
  top: 10px;
  width: 100%; 
  height: 12px; 
  display: flex; 
  justify-content: flex-end;
  align-items: center;
}

@media only screen and (max-width: 930px) {
  #sign-in-section {
    height: 20px;
    margin-bottom: 10px;
  }
}

.sign-in-button {
  margin: 4px;
  border: none;
  outline: none;
  background-color: rgb(37, 157, 201);
  color: white;
  font-family: inherit;
  font-size: 14px;
  border-radius: 4px;
  padding: 4px 8px;
  box-shadow: var(--default-shadow);
}

.sign-in-button:hover {
  background-color: rgb(28, 131, 168);
}

.signed-in-text {
  font-size: 14px;
  color: rgb(114, 114, 114);
  margin-right: 5px;
  font-style: italic;
}
#tutorial-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  animation: fade-in forwards 0.8s linear;
}

#tutorial-container {
  width: 500px;
  display: grid;
  grid-template-columns: 190px 140px;
  grid-template-rows: repeat(3, 150px);
  align-items: center;
  justify-content: center;
  justify-items: center;
  margin: 25px 0 30px 0;
}

.tutorial-item { 
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.tutorial-text {
  display: inline-block;
  justify-self: end;
  text-align: right;
  margin-right: 20px;
  color: rgb(80, 80, 80);
}

#tutorial-check-answers {
  background-color: #AEE3A0;
  color: #36573D;
  font-size: 14px;
  padding: 8px 15px;
  border-radius: 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}
